<template>
    <div>
        This is sign up page
        <div>
            <input type="text" data-toggle="tooltip" data-html="true" title="<em>Tooltip</em> <u>with</u> <b>HTML</b>" value="account" class="form-control" placeholder="ID" v-model="id">
            <input type="text" value="nickname" class="form-control" placeholder="Nickname" v-model="nickname">
            <input type="password" value="password" class="form-control" placeholder="Password" v-model="password">
            <button @click="signup()" class="btn btn-success">Sign Up</button>
        </div>
    </div>
</template>
<script>
import { Md5Enscript } from './../assets/js/common.js';
export default {
    data() {
        return {
            id: '',
            nickname: '',
            password: ''
        }
    },
    methods: {
        signup: function() {
            if(this.password === '') {
                alert("Password not empty!");
            } else {
                let data = {};
                data['id'] = this.id;
                data['nickname'] = this.nickname;
                data['password'] = Md5Enscript(this.password);
                console.log(data);
                $.post({
                    url: 'http://192.168.43.155:8080/user/register',
                    data: JSON.stringify(data), 
                    contentType: 'application/JSON',
                    success: function(result) {
                        console.log(result);
                    }
                });
            }
        }
    }
}
</script>
