<template>
    <div class="container">
        <div class="input-group">
            <select class="custom-select" aria-label="Sect subject" v-model="categoryId">
                <option selected disabled>选择科目</option>
                <option v-for="subject in subjects" :key="subject.id" v-bind:value="subject.id">{{ subject.title }}</option>
                <!-- <option :value="nextSubjectId">其他</option> -->
            </select>
            <div class="input-group-append">
                <button class="btn btn-outline-secondary" data-toggle="modal" data-target="#uploadcsvmodal">确定</button>
            </div>
            <!-- upload file modal -->
            <div class="modal fade" id="uploadcsvmodal" tabindex="-1" role="dialog" aria-labelledby="uploadcsvmodalTitle" aria-hidden="true">
                <div class="modal-dialog modal-dialog-centered" role="document">
                    <div class="modal-content">
                        <div class="modal-header">
                            <h5 class="modal-title" id="uploadcsvmodalTitle">上传科目词库： {{ subjects[categoryId].title }}</h5>
                            <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                                <span aria-hidden="true">&times;</span>
                            </button>
                        </div>
                        <div class="modal-body">
                            <form enctype="multipart/form-data" method="POST" action="http://192.0.59.60:8080/word/upload">
                                <div class="input-group mb-3">
                                    <div class="container-fluid">
                                        <!-- <input type="text" disabled class="form-control" aria-label="subject name" name="categoryId" placeholder="categoryid，默认归为“其他”" :value="categoryId"> -->
                                        <input type="text" class="form-control" aria-label="subject name" name="lexiconName" placeholder="词库名，默认归为“其他”" :value="lexiconName">
                                        <div class="custom-file">
                                            <input type="file" @change="getFile($event)" class="custom-file-input" id="uploadinputaddon">
                                            <label class="custom-file-label" for="uploadinputaddon">浏览文件</label>
                                        </div>
                                        <div class="input-group-append">
                                            <button @click="submitCSV($event)"  class="btn btn-outline-secondary">提交</button>
                                        </div>
                                    </div>
                                </div>
                            </form>
                        </div>
                        <div class="modal-footer">
                            <button type="button" class="btn btn-secondary" data-dismiss="modal">取消</button>
                        </div>
                    </div>
                </div>
            </div>
            <!-- if file uploaded successfully -->
            <div class="modal fade" id="uploadResultModal" tabindex="-1" role="dialog" aria-labelledby="uploadResultModalTitle" aria-hidden="true">
                <div class="modal-dialog modal-dialog-centered" role="document">
                    <div class="modal-content">
                    <div class="modal-header">
                        <h5 class="modal-title" id="uploadResultModalTitle">上传{{ uploadResult }}</h5>
                        <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                        <span aria-hidden="true">&times;</span>
                        </button>
                    </div>
                    <div class="modal-body">
                        {{ uploadNotification }}
                    </div>
                    <div class="modal-footer">
                        <button type="button" class="btn btn-secondary" data-dismiss="modal">Close</button>
                        <button type="button" class="btn btn-primary">Save changes</button>
                    </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
export default {
  data() {
    return {
      subjectName: "其他",
      lexiconName: "Other",
      newSubjectText: "其他",
      subjects: [
        {
          id: 1,
          title: "English"
        },
        {
          id: 2,
          title: "Japanese"
        },
        {
          id: 3,
          title: "Korean"
        },
      ],
      nextSubjectId: 4,
      nextSubject: {
          id: 3 + 1,
          title: '其他'
      },
      categoryId: 0,
      file: '',
      uploadResult: '成功',
      uploadNotification: 'Upload successfully!'
    };
  },
  methods: {
    addNewSubject: function() {
      this.subjects.push({
        id: this.nextSubjectId++,
        title: this.newSubjectText
      });
      this.newSubjectText = "";
    },
    getFile: function(e) {
        // The uploaded file is contained in etarget.files
        this.file = e.target.files[0];
    },
    submitCSV: function(e) {
      e.preventDefault();
      let formData = new FormData();
      formData.append("categoryId", this.categoryId);
      formData.append("lexiconName", this.lexiconName);
      formData.append("file", this.file);

      let config = {
        headers: {
          "Content-Type": "multipart/form-data"
        }
      };

      this.$http.post("http://192.0.59.60:8080/word/upload", formData, config).then(function(res) {
          console.log(res)
        if (res.status === 200) {
            /*这里做处理*/
            $('#uploadcsvmodal').modal('hide');
            this.uploadResult = '成功';
            this.uploadNotification = 'Upload successfully!';
            $('#uploadResultModal').modal('show');
            console.log("Success");
        } else {
            this.uploadResult = '失败';
            this.uploadNotification = 'Upload failed!';
            $('#uploadResultModal').modal('show');
        }
      });
    }
  },
  created: function() {
      this.subjects.push({id: this.subjects.length + 1, title: 'Other'});
  }
};
</script>
<style lang="stylus" scoped>
#uploadcsvmodal {
    input {
        margin-bottom: 10px;
        margin-top: 10px;
    }

    .btn-file-submit {
        margin: 10px;
    }
}
</style>
