            <div class="right-card col">
                <div class="login-welcome">Welcome to KLMA!</div>
                <div class="login-wrapper">
                    <input type="text" class="form-control" placeholder="Username" v-model="account" id="account">
                    <input type="password" class="form-control" placeholder="Password" v-model="password" id="password">
                </div>
                <div class="input-caption">
                    <span class="text-left">还没有账号？<a href="#/signup">去注册</a></span><span class="text-right"><a href="">忘记密码？</a></span>
                </div>
                <button class="btn btn-primary" :disabled="ifDisabled" id="login" @click="login()">登录</button>
                <div class="way-to-login ">
                    <div class="third-party-wrapper">
                        <div class="line-wrapper"></div>
                        <div class="usher-caption">OR</div>
                        <div class="line-wrapper"></div>
