</template>
<script>
import { Md5Enscript } from '@/assets/js/common.js'
import { mapMutations, mapState } from 'vuex'
export default {
    data() {
        return {
            account: '',
            password: '',
            ifDisabled: false
        }
    },
    methods: {
        ...mapMutations([
            'saveUserInfo'
        ]),
        login: function() {
            let account = this.account;
            let password = Md5Enscript(this.password);
            let data = {};
            data['id'] = account;
            data['password'] = password;
            let _this = this
            // console.log(data);
            $.post({
                url: "http://192.168.43.155:8080/user/login",
                data: JSON.stringify(data),
                contentType: 'application/JSON',
                success: function(result) {
                    console.log(result)
                    if(result.code === 0) {
                        console.log(result);
                        console.log(result.data.nickname)
                        _this.saveUserInfo(result.data);
                        window.location.href = '#/setplan';
                    } else {
                        console.log(result.msg)
                        alert(result.msg)
                    }
                }
            }).fail(function(){
                alert("Error")
            });
        },
    },
    computed: mapState({
        userInfo: state => state.userInfo  
    })
}
</script>
