import Vue from 'vue'
import vuex from 'vuex'

Vue.use(vuex)

const state = {
    userInfo: {
        userId: '',
        nickName: '',
        planId: ''
    },
    todayLexiconList: []
}

const mutations = {
    saveUserInfo(state, data) {
        state.userInfo.userId = data.id;
        state.userInfo.nickName = data.nickname;
        state.userInfo.planId = data.planId;
    },
    appendTodayLexiconList(state, data) {
        state.todayLexiconList.append(data.lexiconList);
    }
}

export default new vuex.Store({
    state, 
    mutations,
})